'use client';

import React, { useState } from 'react';

const countries = [
  'United States',
  'United Kingdom',
  'Canada',
  'Australia',
  'Germany',
  'France',
  'Spain',
  'Italy',
  'Sweden',
  'Norway',
  // Add more as needed
];

const languages = [
  'English',
  'Spanish',
  'French',
  'German',
  'Italian',
  'Swedish',
  'Norwegian',
  // Add more as needed
];

const MainPage: React.FC = () => {
  const [showMessage, setShowMessage] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setShowMessage(true);
    
    // Hide message after 3 seconds
    setTimeout(() => {
      setShowMessage(false);
    }, 3000);
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-white">
      <h1 className="text-5xl font-bold mb-12 text-center">SEO GEN</h1>
      
      {showMessage && (
        <div className="mb-6 p-4 bg-green-100 border border-green-400 text-green-700 rounded">
          Input saved successfully!
        </div>
      )}
      
      <form onSubmit={handleSubmit} className="w-full max-w-sm space-y-6 bg-slate-50 p-8 rounded shadow">
        <div>
          <label htmlFor="brand" className="block text-sm font-medium text-slate-700 mb-1">
            Casino Brand Name
          </label>
          <input
            id="brand"
            name="brand"
            type="text"
            required
            className="block w-full px-4 py-2 border border-slate-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder="Enter casino brand name"
          />
        </div>
        <div>
          <label htmlFor="country" className="block text-sm font-medium text-slate-700 mb-1">
            Target Country
          </label>
          <select
            id="country"
            name="country"
            required
            className="block w-full px-4 py-2 border border-slate-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="" disabled selected>
              Select a country
            </option>
            {countries.map((country) => (
              <option key={country} value={country}>
                {country}
              </option>
            ))}
          </select>
        </div>
        <div>
          <label htmlFor="language" className="block text-sm font-medium text-slate-700 mb-1">
            Target Language
          </label>
          <select
            id="language"
            name="language"
            required
            className="block w-full px-4 py-2 border border-slate-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="" disabled selected>
              Select a language
            </option>
            {languages.map((language) => (
              <option key={language} value={language}>
                {language}
              </option>
            ))}
          </select>
        </div>
        <button
          type="submit"
          className="w-full bg-blue-600 text-white py-2 rounded font-semibold hover:bg-blue-700 transition-colors"
        >
          Submit
        </button>
      </form>
    </div>
  );
};

export default MainPage; 